.customLabel {
  display: inline-block;
  cursor: pointer;
  position: relative;
  padding-left: 25px;
  margin-right: 15px;
  font-size: 13px;
}

.wrapper {
  width: 500px;
  margin: 50px auto;
}

input[type=radio],
input[type=checkbox] {
  display: none;
}

.customLabel:before {
  content: "\f0c8";
  font-family: 'FontAwesome';
  color: #8e8e8e;
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 10px;
  position: absolute;
  left: 0;
  bottom: 3px;
  background: transparent;
  border: 1px solid #ececec;
  font-size: 11px;
  line-height: 15px;
  text-align: center;
}

.radio .customLabel:before {
  border-radius: 8px;
}

.checkbox .customLabel {
  margin-bottom: 10px;
}

.checkbox .customLabel:before {
  border-radius: 3px;
}

input[type=radio]:checked + .customLabel:before {
  content: "\f0c8";
  font-family: 'FontAwesome';
  color: #6969e9;
  font-size: 11px;
  text-align: center;
  line-height: 15px;
}

input[type=checkbox]:checked + .customLabel:before {
  content: "\f0c8";
  font-family: 'FontAwesome';
  font-size: 11px;
  color: #6969e9;
  text-align: center;
  line-height: 15px;
}

